"use client"

import { useEffect, useRef, useState } from "react"
import { ChevronLeft, ChevronRight } from "lucide-react"

export default function Team() {
  const [isVisible, setIsVisible] = useState(false)
  const [scrollPosition, setScrollPosition] = useState(0)
  const scrollRef = useRef<HTMLDivElement>(null)
  const ref = useRef(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
        }
      },
      { threshold: 0.2 },
    )

    if (ref.current) {
      observer.observe(ref.current)
    }

    return () => observer.disconnect()
  }, [])

  const teamMembers = [
    {
      name: "Marco De Luca",
      role: "Hairdresser",
      level: "Junior",
      rating: "4.92",
    },
    {
      name: "Enzo Romano",
      role: "Stylist",
      level: "Master",
      rating: "4.6",
    },
    {
      name: "Martina Greco",
      role: "Colorist",
      level: "Senior",
      rating: "4.7",
    },
    {
      name: "Fabio Santoro",
      role: "Beauty Artist",
      level: "Junior",
      rating: "4.4",
    },
    {
      name: "Matteo Ricci",
      role: "Stylist",
      level: "Master",
      rating: "4.8",
    },
    {
      name: "Alessandro Moretti",
      role: "Beauty Artist",
      level: "Senior",
      rating: "4.9",
    },
  ]

  const scroll = (direction: "left" | "right") => {
    if (scrollRef.current) {
      const scrollAmount = 350
      const newPosition =
        direction === "left"
          ? Math.max(0, scrollPosition - scrollAmount)
          : Math.min(scrollRef.current.scrollWidth - scrollRef.current.clientWidth, scrollPosition + scrollAmount)

      scrollRef.current.scrollTo({ left: newPosition, behavior: "smooth" })
      setScrollPosition(newPosition)
    }
  }

  return (
    <section ref={ref} className="py-20 md:py-32 bg-black border-t border-[#53675C]/20">
      <div className="container mx-auto px-4">
        <div className="text-center mb-16">
          <p className="text-[#F8C8DC] text-sm uppercase tracking-widest mb-4">our specialists</p>
          <h2 className="text-4xl md:text-5xl font-bold mb-6">Meet Our Expert Team</h2>
          <p className="text-gray-400 max-w-2xl mx-auto">
            Each specialist is dedicated to understanding your goals, ensuring treatments are tailored to your unique
            beauty needs
          </p>
        </div>

        {/* Team Carousel */}
        <div className="relative">
          <div ref={scrollRef} className="flex gap-6 overflow-x-hidden scroll-smooth">
            {teamMembers.map((member, index) => (
              <div
                key={index}
                className={`flex-shrink-0 w-full sm:w-96 group transition-all duration-500 ${
                  isVisible ? "animate-fade-in" : "opacity-0"
                }`}
                style={{ animationDelay: `${index * 0.1}s` }}
              >
                <div className="relative overflow-hidden rounded-xl h-96 mb-4 border border-[#53675C]/30 group-hover:border-[#F8C8DC]/40 transition-all duration-300">
                  <img
                    src={`/professional-salon-specialist-portrait-.jpg?height=400&width=350&query=professional%20salon%20specialist%20portrait%20${member.level.toLowerCase()}`}
                    alt={member.name}
                    className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-300"
                  />
                  <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent"></div>
                </div>
                <div className="text-center">
                  <h3 className="text-xl font-bold mb-1">{member.name}</h3>
                  <p className="text-[#53675C] text-sm mb-2">{member.level}</p>
                  <p className="text-gray-400 text-sm">{member.role}</p>
                  {member.rating && (
                    <div className="mt-3">
                      <div className="flex justify-center gap-1">
                        {[...Array(5)].map((_, i) => (
                          <span key={i} className="text-[#F8C8DC] text-sm">
                            ★
                          </span>
                        ))}
                      </div>
                      <p className="text-[#53675C] text-xs mt-1 font-semibold">{member.rating}</p>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>

          {/* Navigation Arrows */}
          <button
            onClick={() => scroll("left")}
            className="absolute left-0 top-1/2 -translate-y-1/2 -translate-x-6 bg-[#53675C] text-white p-2 rounded-full hover:bg-[#F8C8DC] hover:text-[#53675C] transition duration-300 z-10"
          >
            <ChevronLeft size={24} />
          </button>
          <button
            onClick={() => scroll("right")}
            className="absolute right-0 top-1/2 -translate-y-1/2 translate-x-6 bg-[#53675C] text-white p-2 rounded-full hover:bg-[#F8C8DC] hover:text-[#53675C] transition duration-300 z-10"
          >
            <ChevronRight size={24} />
          </button>
        </div>
      </div>
    </section>
  )
}
