"use client"

import { useState } from "react"
import { X, Play, Pause, Volume2, VolumeX, Maximize2 } from "lucide-react"

export default function VideoPlayerModal({
  isOpen,
  onClose,
  videoUrl = "Hf6abfL1la4",
}: {
  isOpen: boolean
  onClose: () => void
  videoUrl?: string
}) {
  const [isPlaying, setIsPlaying] = useState(false)
  const [isMuted, setIsMuted] = useState(false)

  if (!isOpen) return null

  return (
    <div className="fixed inset-0 bg-black/80 backdrop-blur-sm z-[999] flex items-center justify-center p-4 animate-fade-in">
      <div className="bg-black w-full max-w-5xl rounded-lg overflow-hidden shadow-2xl animate-scale-up">

        {/* Header */}
        <div className="bg-[#151515] px-6 py-4 flex justify-between items-center border-b border-[#53675C]/30">
          
          <nav className="hidden md:flex gap-8 flex-1">
            {["HOME", "ABOUT", "BLOG", "STORE", "CONTACTS"].map((item) => (
              <a key={item} className="text-gray-400 hover:text-[#53675C] text-sm uppercase transition">
                {item}
              </a>
            ))}
          </nav>

          <div className="flex items-center gap-6">
            <a className="text-[#F8C8DC] text-sm hover:text-[#53675C] transition">
              +91-9840088867
            </a>
            <button className="bg-[#53675C] text-white px-4 py-2 text-sm hover:bg-[#3d4d46] transition">
              AN APPOINTMENT
            </button>

            <button onClick={onClose} className="text-gray-400 hover:text-white transition p-1">
              <X size={24} />
            </button>
          </div>
        </div>

        {/* Video Area */}
        <div className="relative bg-black aspect-video">
          
          {!isPlaying && (
            <>
              <img
                src="/video_parralax.jpg"
                className="w-full h-full object-cover opacity-80"
              />

              <div className="absolute inset-0 flex flex-col items-center justify-center bg-black/40">
                <button
                  onClick={() => setIsPlaying(true)}
                  className="w-20 h-20 bg-white/20 backdrop-blur-md rounded-full flex items-center justify-center hover:bg-white/30 transition border-2 border-white mb-8"
                >
                  <Play size={38} className="text-white ml-1" />
                </button>
                <p className="text-white text-lg tracking-widest">VIDEO TOUR</p>
              </div>
            </>
          )}

          {isPlaying && (
            <iframe
              className="w-full h-full"
              src={`https://www.youtube.com/embed/${videoUrl}?autoplay=1&mute=${isMuted ? 1 : 0}`}
              allow="autoplay; encrypted-media"
              allowFullScreen
            />
          )}

          {/* Controls */}
          {isPlaying && (
            <div className="absolute bottom-0 left-0 right-0 bg-gradient-to-t from-black/80 px-4 py-3">
              <div className="flex justify-between items-center">
                
                <div className="flex gap-4">
                  <button onClick={() => setIsPlaying(false)}>
                    <Pause size={20} className="text-white" />
                  </button>

                  <button onClick={() => setIsMuted(!isMuted)}>
                    {isMuted ? (
                      <VolumeX size={20} className="text-white" />
                    ) : (
                      <Volume2 size={20} className="text-white" />
                    )}
                  </button>
                </div>

                <button>
                  <Maximize2 size={20} className="text-white" />
                </button>
              </div>
            </div>
          )}
        </div>

        {/* Info Bar */}
        <div className="bg-[#151515] px-6 py-4 border-t border-[#53675C]/30">
          <h2 className="text-white font-semibold">
            ANAPLAK Art and Glam Salon – Studio Tour
          </h2>
          <p className="text-gray-400 text-sm mt-1">
            Explore our premium salon facilities and services in Maduravoyal, Chennai.
          </p>
        </div>
      </div>
    </div>
  )
}
