"use client"

import { useEffect, useRef, useState } from "react"

export default function Features() {
  const [isVisible, setIsVisible] = useState(false)
  const ref = useRef(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
        }
      },
      { threshold: 0.2 },
    )

    if (ref.current) {
      observer.observe(ref.current)
    }

    return () => observer.disconnect()
  }, [])

  const services = [
    {
      title: "HAIRSTYLE",
      image: "/elegant-womens-hairstyle-portrait-salon-profession.jpg",
    },
    {
      title: "HAIR COLORING",
      image: "/beautiful-hair-coloring-brunette-highlights-profes.jpg",
    },
    {
      title: "HAIR CUT",
      image: "/modern-short-haircut-womens-professional-salon.jpg",
    },
    {
      title: "STYLING",
      image: "/professional-hair-styling-waves-texture-salon.jpg",
    },
    {
      title: "TREATMENT",
      image: "/hair-treatment-care-salon-professional-beauty.jpg",
    },
    {
      title: "EXTENSIONS",
      image: "/hair-extensions-long-volume-professional-salon.jpg",
    },
  ]

  return (
    <section ref={ref} className="py-20 md:py-32 bg-black border-t border-[#53675C]/20">
      <div className="container mx-auto px-4">
        <div className="text-center mb-12">
          <p className="text-[#F8C8DC] text-xs uppercase tracking-widest mb-4">our unique beauty needs</p>
          <h2 className="text-4xl md:text-5xl font-bold max-w-3xl mx-auto leading-tight mb-6">
            Transform your style with professional hair services
          </h2>
          <p className="text-gray-400 max-w-2xl mx-auto">
            that highlight your natural beauty while keeping your hair strong healthy
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mt-16">
          {services.map((service, index) => (
            <div
              key={index}
              className={`group relative overflow-hidden rounded-lg aspect-[3/4] transition-all duration-500 ${
                isVisible ? "animate-scale-up" : "opacity-0"
              }`}
              style={{ animationDelay: `${index * 0.08}s` }}
            >
              {/* Image */}
              <img
                src={service.image || "/placeholder.svg"}
                alt={service.title}
                className="w-full h-full object-cover transition-transform duration-500 group-hover:scale-110"
              />

              {/* Overlay gradient */}
              <div className="absolute inset-0 bg-gradient-to-t from-black via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300"></div>

              {/* Title at bottom */}
              <div className="absolute bottom-0 left-0 right-0 p-4">
                <p className="text-white font-bold text-sm tracking-widest opacity-0 group-hover:opacity-100 transition-opacity duration-300 translate-y-2 group-hover:translate-y-0">
                  {service.title}
                </p>
              </div>

              {/* Border accent */}
              <div className="absolute inset-0 border border-[#53675C]/0 group-hover:border-[#F8C8DC]/50 transition-all duration-300"></div>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
